import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import {formatDate } from '@angular/common';
import { SocketIoService } from 'src/app/service/socket-io.service';
import { CreateInterview } from 'src/app/models/createInterview';
import { JobService } from 'src/app/service/job.service';

@Component({
  selector: 'app-create-interview',
  templateUrl: './create-interview.component.html',
  styleUrls: ['./create-interview.component.scss']
})
export class CreateInterviewComponent implements OnInit {
 
  id:string;
  idJob:string;
  date7:any;
  today= new Date();
  jstoday = '';
  
  createInterview:CreateInterview;
  constructor(private jobService:JobService,private soketIo:SocketIoService,private route:ActivatedRoute) { }

  ngOnInit() {
    this.createInterview=new CreateInterview();
    
    this.id=this.route.snapshot.params['id'];
    this.idJob=this.route.snapshot.params['idJob'];
    this.createInterview.idUser=parseInt(this.id);
    this.createInterview.idJob=parseInt(this.idJob);
    console.log(this.jstoday);
  }


  interview()
  {
    //ngay gio phong van,requestInterview:true,confirm:true or false,  callBacK:false--se cap nhat tai day
    //phat tin hieu
    this.soketIo.emit("Interview",this.id+this.idJob);
    this.jstoday = formatDate(this.date7, 'dd-MM-yyyy hh:mm:ss a', 'en-US', '+0530');
    this.createInterview.dateInterview=this.jstoday;

    this.jobService.createInterview(this.createInterview)
     .subscribe(res =>{
        
     });
    


  }

}
